/* --- SCSS Variables --- */
// Color Palette
$primary-color: #00385E;
$secondary-color: #DDEBF3;
$hover-color: #A4C8E1;
$success-color: #198754;
$warning-color: #ffc107;
$danger-color: #dc3545;
$info-color: #0dcaf0;

// Order Status Colors
$status-pending: #6c757d;
$status-processing: #0dcaf0;
$status-awaiting: #fd7e14;
$status-shipped: #198754;
$status-completed: #20c997;
$status-cancelled: #dc3545;
$status-hold: #6f42c1;

// Layout & UI Colors
$app-background-color: #f8f9fa;
$container-background-color: #ffffff;
$text-color-primary: #212529;
$text-color-secondary: #495057;
$text-color-muted: #6c757d;
$border-color: #dee2e6;

// Layout & Spacing
$border-radius: 8px;
$base-padding: 1.5rem;
$grid-gap: 1.5rem;
$base-margin: 1rem;
$base-shadow: 0 5px 15px rgba(0, 0, 0, 0.07);

/* --- Main Content Area --- */
.main-content {
    flex-grow: 1;
    padding: $grid-gap;
    overflow-y: auto;
}

/* --- Orders Page Container --- */
.orders-container {
    background-color: $container-background-color;
    border-radius: $border-radius;
    padding: $base-padding;
    border: 1px solid rgba($border-color, 0.5);
}

/* --- Page Header --- */
.page-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: $grid-gap;
    padding-bottom: $base-padding;
    border-bottom: 1px solid $border-color;

    .page-title {
        font-size: 1.75rem;
        font-weight: 600;
        color: darken($text-color-secondary, 20%);
    }
}

/* --- Filter Section --- */
.filter-section {
    margin-bottom: $grid-gap;
    padding: $base-margin;
    background-color: lighten($secondary-color, 5%);
    border-radius: $border-radius;
}

.filter-form {
    display: flex;
    gap: 0.75rem;
    align-items: center;
    flex-wrap: wrap;

    select, input[type="text"], input[type="date"] {
        padding: 0.5rem 0.75rem;
        border: 1px solid $border-color;
        border-radius: $border-radius;
        background-color: $container-background-color;
        min-width: 140px;
        font-size: 0.9rem;

        &:focus {
            outline: none;
            border-color: $primary-color;
            box-shadow: 0 0 0 2px rgba($primary-color, 0.2);
        }
    }
}

/* --- Orders Table --- */
.orders-table-container {
    overflow-x: auto;
    border-radius: $border-radius;
    border: 1px solid $border-color;
    box-shadow: $base-shadow;
}

.orders-table {
    width: 100%;
    border-collapse: collapse;
    font-size: 0.9rem;
    min-width: 800px;

    th, td {
        padding: 0.75rem;
        text-align: left;
        border-bottom: 1px solid $border-color;
        vertical-align: middle;
    }

    thead th {
        background-color: lighten($secondary-color, 3%);
        font-weight: 600;
        color: $text-color-primary;
        border-bottom: 2px solid darken($border-color, 10%);
        position: sticky;
        top: 0;
        z-index: 10;
        white-space: nowrap;
    }

    tbody tr {
        transition: background-color 0.15s ease-in-out;

        &:hover {
            background-color: lighten($secondary-color, 8%);
        }
    }

    .order-number {
        display: flex;
        flex-direction: column;
        gap: 0.2rem;

        strong {
            font-weight: 600;
            color: $text-color-primary;
            font-family: monospace;
        }

        small {
            font-size: 0.75rem;
            color: $text-color-secondary;
            font-family: monospace;
        }
    }

    .customer-info {
        display: flex;
        flex-direction: column;
        gap: 0.2rem;

        strong {
            font-weight: 600;
            color: $text-color-primary;
        }

        small {
            font-size: 0.8rem;
            color: $text-color-secondary;
        }
    }

    .text-center {
        text-align: center;
    }

    .text-right {
        text-align: right;
    }
}

/* --- Order Status Badges --- */
.status-badge {
    display: inline-flex;
    align-items: center;
    gap: 0.25rem;
    padding: 0.3rem 0.6rem;
    border-radius: calc($border-radius / 1.5);
    font-size: 0.8rem;
    font-weight: 500;
    text-transform: uppercase;
    letter-spacing: 0.5px;

    &.status-pending {
        background-color: lighten($status-pending, 40%);
        color: darken($status-pending, 10%);
    }

    &.status-processing {
        background-color: lighten($status-processing, 40%);
        color: darken($status-processing, 20%);
    }

    &.status-awaiting-shipment {
        background-color: lighten($status-awaiting, 40%);
        color: darken($status-awaiting, 20%);
    }

    &.status-shipped {
        background-color: lighten($status-shipped, 40%);
        color: darken($status-shipped, 10%);
    }

    &.status-completed {
        background-color: lighten($status-completed, 40%);
        color: darken($status-completed, 20%);
    }

    &.status-cancelled {
        background-color: lighten($status-cancelled, 30%);
        color: darken($status-cancelled, 10%);
    }

    &.status-on-hold {
        background-color: lighten($status-hold, 40%);
        color: darken($status-hold, 10%);
    }
}

/* --- Progress Indicators --- */
.progress-container {
    display: flex;
    align-items: center;
    gap: 0.5rem;
    margin-bottom: 0.25rem;

    .progress-bar {
        flex-grow: 1;
        height: 8px;
        background-color: lighten($border-color, 5%);
        border-radius: 4px;
        overflow: hidden;
        min-width: 60px;

        .progress-fill {
            height: 100%;
            background: linear-gradient(90deg, $success-color, lighten($success-color, 10%));
            transition: width 0.3s ease-in-out;
            border-radius: 4px;
        }
    }

    .progress-text {
        font-size: 0.75rem;
        font-weight: 600;
        color: $text-color-secondary;
        min-width: 35px;
        text-align: right;
    }
}

/* --- Table Actions --- */
.table-actions {
    display: flex;
    gap: 0.25rem;
    justify-content: center;

    .btn-sm {
        padding: 0.3rem 0.6rem;
        font-size: 0.8rem;

        .material-symbols-outlined {
            font-size: 1rem;
        }
    }
}

/* --- Button Styles --- */
.btn {
    display: inline-flex;
    align-items: center;
    gap: 0.5rem;
    padding: 0.6rem 1.2rem;
    font-size: 0.95rem;
    font-weight: 500;
    border-radius: $border-radius;
    border: 1px solid transparent;
    cursor: pointer;
    transition: all 0.2s ease-in-out;
    text-decoration: none;
    white-space: nowrap;

    .material-symbols-outlined {
        font-size: 1.25rem;
    }

    &:hover {
        transform: translateY(-2px);
        box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
    }
}

.btn-primary {
    background-color: $primary-color;
    color: #fff;
    &:hover { background-color: darken($primary-color, 8%); }
}

.btn-secondary {
    background-color: $text-color-secondary;
    color: #fff;
    &:hover { background-color: darken($text-color-secondary, 8%); }
}

.btn-success {
    background-color: $success-color;
    color: #fff;
    &:hover { background-color: darken($success-color, 8%); }
}

.btn-danger {
    background-color: $danger-color;
    color: #fff;
    &:hover { background-color: darken($danger-color, 8%); }
}

/* --- Modal Styles --- */
.modal {
    display: none;
    position: fixed;
    z-index: 1000;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    background-color: rgba(0, 0, 0, 0.5);
    animation: fadeIn 0.2s ease-in-out;
}

.modal-content {
    background-color: $container-background-color;
    margin: 2% auto;
    padding: 0;
    border-radius: $border-radius;
    width: 90%;
    max-width: 600px;
    max-height: 95vh;
    overflow-y: auto;
    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.2);
    animation: slideIn 0.3s ease-in-out;

    &.modal-large {
        max-width: 900px;
    }

    &.modal-small {
        max-width: 400px;
    }
}

.modal-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: $base-padding;
    border-bottom: 1px solid $border-color;
    position: sticky;
    top: 0;
    background-color: $container-background-color;
    z-index: 10;

    .modal-title {
        margin: 0;
        font-size: 1.25rem;
        font-weight: 600;
        color: $text-color-primary;
    }

    .close {
        background: none;
        border: none;
        font-size: 1.5rem;
        cursor: pointer;
        color: $text-color-secondary;
        padding: 0;
        width: 30px;
        height: 30px;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 50%;
        transition: all 0.2s ease-in-out;

        &:hover {
            background-color: lighten($danger-color, 40%);
            color: $danger-color;
        }
    }
}

.modal-body {
    padding: $base-padding;

    p {
        margin-bottom: $base-margin;
        line-height: 1.5;
    }
}

/* --- Form Styles --- */
.form-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: $base-margin;
}

.form-section {
    margin-bottom: $base-padding;

    .section-header {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: $base-margin;
        padding-bottom: 0.5rem;
        border-bottom: 1px solid $border-color;

        h3 {
            margin: 0;
            font-size: 1.1rem;
            font-weight: 600;
            color: $text-color-primary;
        }
    }
}

.form-group {
    margin-bottom: $base-margin;

    .form-label {
        display: block;
        margin-bottom: 0.5rem;
        font-weight: 500;
        color: $text-color-primary;
    }

    .form-input, .form-textarea, select {
        width: 100%;
        padding: 0.75rem;
        border: 1px solid $border-color;
        border-radius: $border-radius;
        font-size: 0.95rem;
        transition: all 0.2s ease-in-out;

        &:focus {
            outline: none;
            border-color: $primary-color;
            box-shadow: 0 0 0 3px rgba($primary-color, 0.1);
        }

        &::placeholder {
            color: lighten($text-color-secondary, 20%);
        }
    }

    .form-textarea {
        resize: vertical;
        min-height: 80px;
    }
}

.form-actions {
    display: flex;
    gap: $base-margin;
    justify-content: flex-end;
    padding: $base-padding;
    border-top: 1px solid $border-color;
    background-color: lighten($secondary-color, 8%);
    margin: 0;
    position: sticky;
    bottom: 0;
}

/* --- Order Items Management --- */
.order-item {
    margin-bottom: $base-margin;
    padding: $base-margin;
    border: 1px solid $border-color;
    border-radius: $border-radius;
    background-color: lighten($secondary-color, 8%);

    .item-grid {
        display: grid;
        grid-template-columns: 2fr 1fr 1fr auto;
        gap: 0.75rem;
        align-items: end;

        @media (max-width: 768px) {
            grid-template-columns: 1fr;
        }
    }
}

/* --- Order Details Display --- */
.order-details {
    padding: $base-padding;

    .details-grid {
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: $base-padding;
        margin-bottom: $base-padding;

        @media (max-width: 768px) {
            grid-template-columns: 1fr;
        }
    }

    .detail-section {
        h4 {
            margin: 0 0 0.75rem 0;
            font-size: 1rem;
            font-weight: 600;
            color: $text-color-primary;
            border-bottom: 1px solid $border-color;
            padding-bottom: 0.25rem;
        }

        p {
            margin: 0.5rem 0;
            font-size: 0.9rem;
            line-height: 1.4;

            strong {
                color: $text-color-primary;
                margin-right: 0.5rem;
            }
        }
    }

    .items-section {
        margin-bottom: $base-padding;

        h4 {
            margin: 0 0 0.75rem 0;
            font-size: 1rem;
            font-weight: 600;
            color: $text-color-primary;
        }
    }

    .notes-section {
        h4 {
            margin: 0 0 0.75rem 0;
            font-size: 1rem;
            font-weight: 600;
            color: $text-color-primary;
        }

        p {
            background-color: lighten($secondary-color, 5%);
            padding: $base-margin;
            border-radius: $border-radius;
            border-left: 3px solid $primary-color;
            margin: 0;
            line-height: 1.5;
        }
    }
}

.details-table {
    width: 100%;
    border-collapse: collapse;
    font-size: 0.85rem;
    border: 1px solid $border-color;
    border-radius: $border-radius;
    overflow: hidden;

    th, td {
        padding: 0.5rem 0.75rem;
        text-align: left;
        border-bottom: 1px solid $border-color;
    }

    thead th {
        background-color: lighten($secondary-color, 3%);
        font-weight: 600;
        color: $text-color-primary;
    }

    tbody tr:hover {
        background-color: lighten($secondary-color, 8%);
    }
}

/* --- Message Styles --- */
.message {
    padding: $base-margin;
    margin-bottom: $grid-gap;
    border-radius: $border-radius;
    border: 1px solid;
    font-weight: 500;

    &.success {
        background-color: lighten($success-color, 40%);
        border-color: darken(lighten($success-color, 40%), 10%);
        color: darken($success-color, 10%);
    }

    &.error {
        background-color: lighten($danger-color, 30%);
        border-color: darken(lighten($danger-color, 30%), 10%);
        color: darken($danger-color, 10%);
    }
}

/* --- Empty State --- */
.empty-state {
    text-align: center;
    padding: 3rem;
    color: $text-color-secondary;

    .material-symbols-outlined {
        font-size: 4rem;
        margin-bottom: $base-margin;
        opacity: 0.6;
    }

    h3 {
        margin-bottom: 0.5rem;
        color: $text-color-primary;
    }

    p {
        font-size: 0.95rem;
    }
}

/* --- Loading States --- */
.loading {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 2rem;
    color: $text-color-secondary;

    &::after {
        content: '';
        width: 20px;
        height: 20px;
        border: 2px solid lighten($primary-color, 40%);
        border-top: 2px solid $primary-color;
        border-radius: 50%;
        animation: spin 1s linear infinite;
        margin-left: 0.5rem;
    }
}

/* --- Animations --- */
@keyframes fadeIn {
    from { opacity: 0; }
    to { opacity: 1; }
}

@keyframes slideIn {
    from {
        transform: translateY(-50px);
        opacity: 0;
    }
    to {
        transform: translateY(0);
        opacity: 1;
    }
}

@keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
}

/* --- Responsive Design --- */
@media (max-width: 768px) {
    .filter-form {
        flex-direction: column;
        align-items: stretch;

        select, input[type="text"], input[type="date"] {
            min-width: auto;
        }
    }

    .page-header {
        flex-direction: column;
        gap: $base-margin;
        align-items: stretch;
    }

    .orders-table-container {
        font-size: 0.8rem;
    }

    .orders-table {
        th, td {
            padding: 0.5rem;
        }
    }

    .form-grid {
        grid-template-columns: 1fr;
    }

    .progress-container {
        .progress-bar {
            min-width: 40px;
        }

        .progress-text {
            min-width: 30px;
        }
    }

    .table-actions {
        flex-direction: column;
        gap: 0.25rem;

        .btn-sm {
            font-size: 0.75rem;
            padding: 0.25rem 0.5rem;
        }
    }

    .status-badge {
        font-size: 0.7rem;
        padding: 0.2rem 0.4rem;
    }

    .modal-content {
        margin: 1% auto;
        width: 95%;
    }
}

@media (max-width: 480px) {
    .orders-table {
        min-width: 600px; // Force horizontal scroll on very small screens
    }

    .order-item .item-grid {
        grid-template-columns: 1fr;
        gap: 0.5rem;
    }

    .details-grid {
        grid-template-columns: 1fr !important;
    }
}